q:before, q:after {
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* box-sizing fix */
/* stylelint-disable-next-line no-duplicate-selectors */
body {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

/* root font size */
:root {
  font-size: 1em;
}

/* universal max-width rule for images */
img {
  max-width: 100%;
}

/* remove default focus styles */
a, button, input {
  outline: 0;
}

a::-moz-focus-inner, button::-moz-focus-inner, input::-moz-focus-inner {
  outline: 0;
  border: 0;
}

