/**
 * Styles for the header block.
 */
.header {
  background-image: url("../img/42894.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-color: #303B6C;
  width: 100%;
  height: 100%;
  position: relative;
}

@media (max-width: 768px) {
  .header {
    padding: 0 20px;
    background-position: 65%;
  }
}

.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__top_icon {
  margin-top: 22px;
  display: flex;
  align-items: center;
}

.header__top_icon-text {
  font-family: 'Cera Pro';
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 20px;
  letter-spacing: 1px;
  text-transform: uppercase;
  max-width: 155px;
  margin-left: 9px;
  color: #FFFFFF;
}

@media (max-width: 768px) {
  .header__top_icon-text {
    font-size: 14px;
    max-width: 121px;
  }
}

.header__top_phone {
  display: flex;
}

@media (max-width: 768px) {
  .header__top_phone {
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 120px;
    left: 30px;
  }
}

@media (max-width: 768px) {
  .header__top_phone-img {
    display: none;
  }
}

.header__top_phone-text {
  margin-left: 6px;
  font-family: 'Futura';
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  color: #FFFFFF;
  text-decoration: none;
}

.header__top_phone-text:hover {
  opacity: .5;
}

.header__top_link {
  background: linear-gradient(169.44deg, #F03C53 0%, #FF2778 101.5%), #F03C53;
  mix-blend-mode: normal;
  opacity: 0.65;
  box-shadow: 0px 0px 20px #4A5897;
  border-radius: 15.5px;
  text-decoration: none;
  font-family: 'Museo Sans Cyrl';
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  padding: 6px 23px 8px 25px;
  line-height: 17px;
  color: #FFFFFF;
  border: none;
  cursor: pointer;
}

@media (max-width: 768px) {
  .header__top_link {
    position: absolute;
    top: 135px;
    right: 20px;
  }
}

.header__top_link:hover {
  opacity: .5;
}

.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__nav_list {
  margin-top: 36px;
  margin-bottom: 48px;
  display: flex;
  align-items: center;
}

@media (max-width: 768px) {
  .header__nav_list {
    flex-direction: column;
    display: none;
  }
}

.header__nav_item {
  display: flex;
  align-items: center;
}

.header__nav_item:not(:last-child):after {
  content: '|';
  display: block;
  color: #fff;
  margin: 0 22px;
}

@media (max-width: 768px) {
  .header__nav_item:not(:last-child):after {
    display: none;
  }
}

.header__nav_item-link {
  font-family: 'Cera Pro';
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 16px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  text-decoration: none;
}

.header__nav_item-link:hover {
  opacity: .5;
}

.header__nav_btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 21px 5px 15px;
  background-color: #303B6C;
  border-radius: 15.5px;
}

@media (max-width: 768px) {
