}

.header__nav_btn-link {
  font-family: 'Cera Pro';
  font-style: normal;
  font-weight: normal;
  text-decoration: none;
  font-size: 14px;
  line-height: 18px;
  color: #FFFFFF;
}

@media (max-width: 768px) {
