  }
}

.modal {
  display: none;
  position: fixed;
  z-index: 16;
  padding-top: 39px;
  left: 40%;
  top: 20%;
  width: 432px;
  height: 487px;
  background: #FFFFFF;
  box-shadow: 0px 0px 49px #525567;
  border-radius: 81px 81px 17px 81px;
}

@media (max-width: 768px) {
  .modal {
    max-width: 288px;
    height: 352px;
    top: 6%;
    left: 3%;
    padding-top: 32px;
    border-radius: 54px 54px 17px 54px;
  }
}

#bg {
  position: absolute;
  width: 1000%;
  height: 1000%;
  top: 0;
  left: 0;
  display: none;
  background: black;
  opacity: .7;
  z-index: 16;
}

.modal-content {
  padding: 0 20px;
}

.modal-content__title {
  font-family: 'Cera Pro';
  font-style: normal;
  font-weight: bold;
  font-size: 22px;
  line-height: 28px;
  text-align: center;
  color: #202035;
}

@media (max-width: 768px) {
  .modal-content__title {
    font-size: 15px;
  }
}

.modal-content__form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.modal-content__input {
  width: 340px;
  height: 50px;
  border: 1px solid #303B6C;
  border-radius: 29px;
  font-family: 'Cera Pro';
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  margin-top: 20px;
  color: #303B6C;
}

@media (max-width: 768px) {
  .modal-content__input {
    width: 227px;
    height: 40px;
    margin-top: 10px;
    font-size: 13px;
  }
}

.modal-content__btn {
  width: 340px;
  height: 58px;
  background: linear-gradient(170.23deg, #F03C53 0%, #FF2778 101.5%), #F03C53;
  box-shadow: 0px 0px 20px #A09BC2;
  border-radius: 29px;
  font-family: 'Cera Pro';
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 23px;
  text-align: center;
  outline: none;
  border: none;
  cursor: pointer;
  margin-top: 50px;
  color: #FFFFFF;
}

@media (max-width: 768px) {
  .modal-content__btn {
    width: 227px;
    height: 40px;
    margin-top: 24px;
    font-size: 15px;
  }
}

.modal-content__btn:hover {
  opacity: .5;
}

.modal-content__close {
  color: #aaaaaa;
  float: right;
  margin-top: -45px;
  margin-right: -20px;
  font-size: 28px;
  font-weight: bold;
}

.modal-content__close:hover, .modal-content__close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}

@media (max-width: 640px) {
  .menu {
    display: none;
  }
  .trigger {
    display: inline-block;
    position: absolute;
    top: 40px;
    right: 20px;
    cursor: pointer;
    z-index: 16;
  }
  .trigger i {
    display: block;
    width: 25px;
    height: 2px;
    margin: 6px auto;
    background: #fff;
  }
  .show {
    display: block;
    position: absolute;
    background: #303B6C;
    mix-blend-mode: normal;
    opacity: 0.95;
    width: 100%;
    height: 100%;
    left: 0;
    z-index: 15;
    top: -4%;
    display: flex;
    padding-top: 100px;
  }
  .close i {
    margin: 0;
  }
  .close i:nth-child(1) {
    transform: rotateZ(45deg);
    position: relative;
    top: 16px;
  }
  .close i:nth-child(2) {
    transform: rotateY(90deg);
  }
  .close i:nth-child(3) {
    transform: rotateZ(-45deg);
    top: 12px;
    position: relative;
  }
  .header__nav_item {
    margin-top: 30px;
  }
  .header__nav_item-link {
    font-family: 'Cera Pro';
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 40px;
    /* or 286% */
    text-align: center;
    color: #FFFFFF;
  }
  .nav__close {
    color: #aaaaaa;
    position: absolute;
    top: 20px;
    right: 20px;
    z-index: 20;
    font-size: 28px;
    font-weight: bold;
  }
  .nav__close:hover,
  .nav__close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
  }
}

